* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  /* background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
  color: white; */
  background: url("./img/shield.png");
  /* Set a specific height */
  min-height: 500px;

  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: "Poppins", sans-serif;
}

.searchArea {
  display: flex;
  justify-content: center;
  align-items: center;
}

.searchBar {
  display: flex;
  width: 100%;
}

form {
  min-height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40%;
}
form input,
form button {
  padding: 0.5rem;
  font-size: 2rem;
  border: none;
  background: white;
}
form button {
  color: #171717;
  background: #f7fffe;
  cursor: pointer;
  transition: all 0.3s ease;
}
form button:hover {
  background: #171717;
  color: white;
}
