.trash-btn,
.complete-btn {
  background: #171717;
  color: white;
  border: none;
  padding: 1rem;
  cursor: pointer;
  font-size: 1rem;
}
.complete-btn {
  background: rgb(11, 212, 162);
}
.todo-item {
  padding: 0rem 0.5rem;
}

.todo-input {
  width: 100%
}

.fa-trash,
.fa-check {
  pointer-events: none;
}

.fall {
  transform: translateY(10rem) rotateZ(20deg);
  opacity: 0;
}

.completed {
  text-decoration: line-through;
  opacity: 0.5;
}

/*CUSTOM SELECTOR */
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background-image: none;
}

/* Custom Select */
.select {
  margin: 1rem;
  position: relative;
  overflow: hidden;
}
select {
  color: #171717;
  font-family: "Poppins", sans-serif;
  cursor: pointer;
  width: 12rem;
}
/* Arrow */
.select::after {
  content: "\25BC";
  position: absolute;
  top: 0;
  right: 0;
  padding: 1rem;
  background: #171717;
  cursor: pointer;
  pointer-events: none;
}
/* Transition */
/* Landing Page */
.landing_container {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  grid-template-areas: ". box box ."
                       ". qoute qoute ."
                       ". enter enter ."
                       ". enter enter .";
  width: 100vw;
  height: 100vh;
  font-family: 'Titillium Web', sans-serif;

}

#landingText {
  letter-spacing: -4px;
}

.enterArea {
  grid-area: box;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 3em;
}

.quoteArea {
  grid-area: qoute;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}

.enterBTNDiv{
  grid-area: enter;
  display: flex;
  align-items: center;
  justify-content: center;
}

.enterBTNDiv  button {
  font-size: 2em;
  padding: .5em 1em;
  border-radius: 5px 5px 5px 5px;
}


/*
Container styles
*/

.textDiv {
  text-align: center;
  color: #f6ea00;
  font-family: 'Titillium Web', sans-serif;
  font-size: 2em;
  text-shadow: 0 0 32px red
}
.container {
  width: 1000px;
  margin: 0 auto;

}

.mainContainer {
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;

}

.gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: auto;
  grid-gap: 1rem;
  justify-items: center;
}


/* Card Styles */
.card {
  background: #ed1d24;
  padding: 5px;
  margin: 2rem;
  /* flex-grow: 1; */
  height: 150px;
  width: 50%;
  font-family: 'Titillium Web', sans-serif;
  font-weight: bold;
  font-size: 1em;
  text-align: center;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-template-areas: "main main main"
                        "side1 side1 side2";
  gap: 1em;
  transition: .7s;
  border:solid 2px #000;
  box-shadow:0 6px 6px -6px #000;
}

 h3 {
  color: #f6ea00;
}

p {
  color: #f6ea00;
}


.card.active {
  margin: 0;
  height: 400px;
  width: 100%;
  background: whitesmoke;
}

.cardbkg {
  background-color: #ed1d24;
}

.mainBorder {
  display: flex;
  grid-area: main;
  /* border:solid 2px #000; */
  /* box-shadow:0 6px 6px -6px #000; */
  position: relative;
  top: 100%;
  transform: translateY(-50%);
  justify-content: center;
  align-items: center;
}

.mainBorder.active {
  position: relative;
    border:solid 2px #000;
  box-shadow:0 6px 6px -6px #000;
  top: -50%;
  transform: translateY(+50%);
}

.side1 {
  display: none;
  padding: .5em;
  transition: .7s;
}
.side2 {
  display: none;
  transition: .7s;
}

.side1.active {
  display: inline;
  grid-area: side1;
  border:solid 2px #000;
  box-shadow:0 6px 6px -6px #000;
}
.side2.active {
  display: inline;
  grid-area: side2;
  border:solid 2px #000;
  box-shadow:0 6px 6px -6px #000;
}

img {
  height: 100%;
  width: 100%;
}

.landing_container {
  background: url("./img/red.jpg");
  background-size:cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100vw;
  height: 100vh;
}

@media only screen and (max-width: 600px) {
  .loading_container {
    font-size: 1.2em;
  }

  .landing_container {
    grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-areas: "box box"
                       "qoute qoute"
                       "enter enter";
  }
  .enterArea {
    font-size: 1.5em;

  }

  .quoteArea {
    display: none;
  }

  form {
    width: 60%;
  }
}

@media only screen and (max-width: 600px) {
  .gallery {
    display: grid;
    grid-template-columns: 1fr;
}}